                        <span v-show="arr.review==4||arr.review==3||arr.review==2"><img src="image/details/mini.png" alt=""></span>
                        <span v-show="arr.review==3||arr.review==2"><img src="image/details/mini.png" alt=""></span>
                        <span v-show="arr.review==2||arr.review==1"><img src="image/details/mini.png" alt=""></span>
                        <span v-show="arr.review==1"><img src="image/details/mini.png" alt=""></span>
                    </p>
                </div>
                <p><span v-text="arr.newchapter"></span>2018-12-24 14:36:32</p>
                <p></p>
                <div>
                    <a href="javascript:;"></a>
                    <a href="javascript:;" @click="insertBook" :style="disable">{{InsertBook}}</a>
                    <a href="javascript:;"></a>
                    <a href="javascript:;"></a>
                </div>
            </div>
        </div>
        <div class="detail-content-nav">
            <h3 class="active"></h3>
            <h3></h3>
            <h3></h3>
        </div>
        <div class="detail-list">
            <div class="detail-list-title">
                <h3></h3>
            </div>
            <ul class="detail-ul">
                <li v-for="(item,i) of recomListOne" :key="i"><span>[{{item.story_family}}]</span><span style="color:#333;cursor:pointer" @click="toDetail(item.sid)">{{item.bname}}</span><span v-text="item.author"></span></li>
            </ul>
            <div class="detail-list-title">
                <h3></h3>
            </div>
            <ul class="detail-ul">
                <li v-for="(p,i) of recomListTwo" :key="i"><span>[{{p.story_family}}]</span><span style="color:#333;cursor:pointer" @click="toDetail(p.sid)">{{p.bname}}</span><span v-text="p.author"></span></li>
            </ul>
            <div class="detail-list-title">
                <h3></h3>
            </div>
            <ul class="detail-ul">
                <li v-for="(p,i) of recomListThree" :key="i"><span>[{{p.story_family}}]</span><span style="color:#333;cursor:pointer" @click="toDetail(p.sid)">{{p.bname}}</span><span v-text="p.author"></span></li>
            </ul>
        </div>
        <div class="detail-middle">
            <div class="detail-intr">
                            <p ref="pHeight" :style="pStyle">
                            
                              
                              
                              
                              
                             
                              
                               
                              
                              
                              
                        </p>
                        <button @click="orderTo">{{btnContent}}<img :src="`image/${clickBtn}.png`" alt=""></button>
                    
            </div>
            <div class="detail-middle-title">
                <h3></h3>
            </div>
            <div class="detail-middle-content">
                <div class="detail-middle-left">
                    <ul>
                        <li><span>></span></li>
                        <li><span>1</span>olivia<span>23423432</span></li>
                        <li><span>2</span><span>12354323</span></li>
                        <li><span>3</span>alleyliu<span>8424344</span></li>
                        <li><span>4</span>s<span>7456834</span></li>
                        <li><span>5</span><span>6434523</span></li>
                        <li><span>6</span>XIAO<span>6123412</span></li>
                        <li><span>7</span>QQ28d9b046eac<span>5975245</span></li>
                        <li><span>8</span>w<span>5432233</span></li>
                        <li><span>9</span><span>4553433</span></li>
                        <li><span>10</span>jinging<span>3423453</span></li>
                    </ul>
                </div>
                <div class="detail-middle-center">
                    <div>
                        <div>
                            <span></span><span></span>
                        </div>
                        <p></p>
                        <p>2821</p>
                        <a href="javascript:;"></a>
                    </div>
                    <div>
                        <p></p>
                        <p></p>
                        <p>17027</p>
                        <a href="javascript:;"></a>
                    </div>
                </div>
                <div class="detail-middle-right">
                    <p></p>
                    <p><span>1</span></p>
                    <p><span>3</span></p>
                    <p>szhappytime<span>1</span></p>
                    <p>cuihonghua1<span>2</span></p>
                    <p>321<span>5</span></p>
                    <p>szhappytime<span>1</span></p>
                    <p>limin197903<span>1</span></p>
                    <p><span>3</span></p>
                    <p><span>1</span></p>
                    <p>1314<span>4</span></p>
                </div>
            </div>
            <div class="detail-bottom"></div>
            <div class="detail-bottom-content">
                <div class="bottom-content" v-for="(p,i) of recomListFour" :key="i">
                    <div @click="toDetail(p.sid)">
                        <img :src="`http://127.0.0.1:1994/${p.imgUrl}`" alt="">
                    </div>
                    <div>
                        <p v-text="p.bname" @click="toDetail(p.sid)" style="cursor:pointer"></p>
                        <p>{{p.author}}/</p>
                        <p>
                            
                              
                              
                              
                              
                             
                              
                               
                              
                              
                              
                        </p>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <my-footer style="margin-top:150px;"></my-footer>
    <login :spanHid="spanHid" :loginMsg="loginMsg" :log="log" :imgUrl="imgUrl" :changeImg="changeImg" :toLogin="toLogin" v-on:sendValue="(val)=>this.authCode=val" @sendName="(val)=>this.uname=val" @sendPwd="(val)=>this.upwd=val"></login>   <!---->
    </div>
</template>
<script>
import { setTimeout } from 'timers';
export default {
    data(){
        return {
            authCode:"", //
            arr1:this.$store.state.arr,    //
            imgUrl:1,       //
            log:null,    //
            // fdjStyle:{
            //     display:"none",
            // },
            spanStyle:{
                top:0,
                left:0,
            },
            imgStyle:{
                top:0,
                left:0,
            },
            isNone:false,
            widthOne:0,     //
            heightOne:0,
            divWidth:0,     //
            divHeight:0,
            arr:{imgUrl:"gy/dnyf.jpg"},
            fname:{},
            loginMsg:"",
            authTrue:false,     //
            uname:"",
            upwd:"",
            InsertBook:"",
            bookShelfTrue:false,
            disable:{},
            recomList:[],
            recomFamily:[],
            recomListOne:[],
            recomListTwo:[],
            recomListThree:[],
            recomListFour:[],
            num:0,
            clickBtn:"toBottom",
            can:null,
            btnContent:"",
            Pheight:0,
            pStyle:{},
        }
    },
    watch:{
        authCode(){
            if(this.arr1[this.imgUrl-1]==this.authCode){
                this.authTrue=true;
            }else{
                this.authTrue=false;
            };

        },
    },
    created(){
        this.getDetali();
        this.getBookShelf();
        this.getrecomlist();
    },
    methods:{ 
        orderTo(){
            if(this.can==true){
                this.btnContent="";
                this.clickBtn="toTop";
                this.pStyle={
                    height:"auto",
                }
                this.can=false;
            }else if(this.can==false){
                this.btnContent="";
                this.clickBtn="toBottom";
                this.pStyle={
                    height:"300px",
                    overflow:"hidden",
                }
                this.can=true;
            }
        },
        pHet(){
            if(this.Pheight>300){
                this.pStyle={
                    height:"300px",
                    overflow:"hidden",
                }
                this.can=true;
            }
        },
        toDetail(i){
            this.$router.push(`/detail/${i}`)
            location.reload();
        },
        getrecomlist(){
            this.axios.get("recomlist").then(res=>{
                this.recomList=res.data.data[0];
                this.recomFamily=res.data.data[1];          //
                console.log(this.recomList)
                for(var i=0;i<this.recomList.length;i++){
                    //story_famile   family_id11
                    this.recomList[i].story_family=this.recomFamily[this.recomList[i].family_id-1].fname;
                }
                this.recomListOne=this.recomList.splice(0,10);
                this.recomListTwo=this.recomList.splice(0,10);
                this.recomListThree=this.recomList.splice(0,10);
                this.recomListFour=this.recomList.splice(0);
            })
        },
        getBookShelf(){
            console.log(this.sid)
            this.axios.get("getBookShelf",{
                params:{sid:this.sid}
            }).then(res=>{
                if(res.data.code==1){
                    this.InsertBook="";
                    this.bookShelfTrue=true;
                    this.disable={
                        background:"#ffffff",
                        color:"#a6a6a6",
                        "border-color":"#a6a6a6",
                    }
                }else{
                    this.InsertBook="";
                    this.bookShelfTrue=false;
                }
            })
        },
        insertBook(){
            var sid = this.arr.sid;
            var bname = this.arr.bname;
            var story_family = this.fname.fname;
            var newchapter = this.arr.newchapter;
            var author = this.arr.author;
            var joinTime = new Date().toLocaleDateString();
            var obj = this.qs.stringify({
                sid,
                bname,
                story_family,
                newchapter,
                author,
                joinTime,
            })
            if(this.bookShelfTrue){
                return;
            }else{
                this.axios.post("/userBook",obj).then(res=>{
                    if(res.data.code==-1){
                        this.$message.error("");
                        setTimeout(()=>{
                            this.log=1;
                        },1000)
                    }else{
                        this.$alert("","").then(act=>{
                            // location.reload();
                            this.getBookShelf();
                        }).catch(err=>{
                            this.getBookShelf();
                            // location.reload();
                        });
                    }
                });
            }
        },
        getDetali(){
            this.axios.get("detail",{
                params:{
                    sid:this.sid
                }
            }).then(res=>{
                console.log(res.data.data)
                this.arr=res.data.data[0][0];
                this.fname=res.data.data[1][0]
                console.log(this.fname.fname)
                this.num=this.fname.fname==""?1:this.fname.fname==""?2:this.fname.fname==""?3:this.fname.fname==""?4:5
                console.log(this.num)
            })
        },
        fdjMove(e){
            var top=e.offsetY-71;
            var left=e.offsetX-52;
            if(top<0){
                top=0;
            }else if(top>285-142){
                top=285-142;
            }
            if(left<0){
                left=0;
            }else if(left>210-105){
                left=210-105;
            }
            this.spanStyle={
                top:top+"px",
                left:left+"px",
            };
            this.imgStyle={
                top:-2*top+"px",
                left:-2*left+"px",
            }
        },
        fdjDiv(){
            this.isNone=!this.isNone;
        },
        outLogin(){
            this.axios.get("outlogin").then(result=>{
                if(result.data.code==1){
                    location.reload();
                    this.$message("");
                    sessionStorage.removeItem("uname");
                    sessionStorage.removeItem("nickName");
                };
            });
        },
        toLogin(){          //
            if(this.uname==""&&this.upwd==""){
                this.loginMsg="";
                return;
            }else if(this.uname==""){
                this.loginMsg="";
                return;
            }else if(this.upwd==""){
                this.loginMsg="";
                return;
            }else if(this.authCode==""){
                this.loginMsg="";
                return;
            };
            if(!this.authTrue){
                this.loginMsg="";
            }else{
                this.axios.get("login",{
                    params:{
                        uname:this.uname,
                        upwd:this.upwd,
                    }
                }).then(res=>{
                    if(res.data.code==1){
                        var uname=(res.data.data[0]).slice(-4);
                        var nickName=res.data.data[1]
                        sessionStorage.setItem("uname","xxmy"+uname);
                        sessionStorage.setItem("nickName",nickName)
                        this.$alert("","",{confirmButtonText:''}).then(active=>{ 
                            this.log=null;
                            location.reload();
                        }).catch(err=>{
                            this.log=null;
                            location.reload();
                        });
                    }else{
                        this.$message.error("");
                    }
                });
            }
        },
        changeImg(){        //
            this.imgUrl++;
            if(this.imgUrl==7){
                this.imgUrl=1; 
            };
        },
        login(){            //
            this.log=1;     
        },
        spanHid(){          //
            this.log=null;   
        },
    },
    mounted(){
        // var widthOne=this.$refs.divOne.offsetWidth;
        this.Pheight=this.$refs.pHeight.offsetHeight;
        console.log(this.Pheight)
        this.pHet();
    },
    props:["sid"], 
}
</script>
<style>
    @import url("../assets/css/detalis.css");
</style>
