<template>
    <div>
    <div class="reg-cont">
        <div><router-link to="/"><img src="image/index/logo.png" alt=""></router-link></div>
        <div class="reg">
            <div><h3></h3></div>
            <div class="reg-content">
                <div class="reg-int">
                    <div>
                        <span></span><input type="text" v-model="uname" @focus="unameFocus" @blur="unameBlur"><span :class="styleInt">{{msgUname}}</span>
                    </div>
                    <div style="position:relative">
                        <span></span><input :type="intType" v-model="upwd" @focus="upwdFocus" @blur="upwdBlur" maxlength="20"><span class="eyeSpan" :style="eyeStyle" @click="showPwd"><img :src="`image/yanjing${showImg}.png`" alt=""></span><span :class="styleUpwd">{{msgUpwd}}</span><span :class="spanStyle"><span :class="{isUpwd:isupwd==1}"></span><span :class="{isUpwd:isupwd==2}"></span><span :class="{isUpwd:isupwd==3}"></span><span>{{upwdAuth}}</span></span>
                    </div>
                    <div>
                        <span></span><input type="password" v-model="pwd" @focus="pwdFocus" @blur="pwdBlur" maxlength="20" @input="pwdAuth"><span :class="isTrue">{{msgPwd}}</span>
                    </div>
                    <div>
                        <span></span><input type="text" v-model="auth"><span @click="changeImg"><img :src="`image/yan${imgNum}.png`" alt=""></span><span @click="changeImg"></span>
                    </div>
                    <div>
                        <label for="s1"><input type="checkbox"  id="s1" v-model="isAgree"><span></span></label><span></span>
                    </div>
                    <button @click="userReg"></button>
                </div>
                <div class="reg-right">
                    <p><router-link to="/login">>></router-link></p>
                </div>
            </div>
        </div>
    </div>
    </div>
</template>
<script>
import { URLSearchParams } from 'url';
export default {
    data(){
        return {
            widthout:0,
            imgNum:Math.ceil(Math.random()*6),    //
            arr:this.$store.state.arr,    //
            auth:"",    //
            uname:"",   //
            upwd:"",    //
            pwd:"",     //
            msgUname:"",     //
            msgUpwd:"",     //
            msgPwd:"",      //
            upwdAuth:"",
            styleInt:{      //
                'int-focus':true,
                'int-success':false,
                'int-err':false,
            },
            styleUpwd:{     //
                'upwd-focus':false,
                'dis-none':true,
            },
            isupwd:0,
            spanStyle:{     //
                'span-sp':false,
                'dis-none':true,
            },
            isTrue:{
                'int-focus':true,
                'is-success':false,
                'is-err':false,
            },
            unameIstrue:false,      //true
            upwdIstrue:false,
            pwdIstrue:false,
            authIstrue:false,
            isAgree:false,
            eyeStyle:{
                display:"none",
            },
            intType:"password", //
            int6:false,     //
            int5:false,
            showImg:1,     //
            int6:true,
            int5:true,
        }
    },
    watch:{
        // widthout(){
        //     console.log(this.widthout)
        // },
        uname(){    //
            var reg=/^1[3-8]\d{9}$/     //
            if(!reg.test(this.uname)){
                this.msgUname=""
                this.styleInt={
                    'int-focus':true,
                    'int-success':false,
                    'int-err':true,
                }
            }else{
                this.msgUname=""
                this.styleInt={
                    'int-focus':true,
                    'int-success':true,
                    'int-err':false,
                }
             };
        },
        auth(){     //
            if(this.auth==this.arr[this.imgNum-1]){
                this.authIstrue=true;
            }else{
                this.authIstrue=false;
            }
        },
        upwd(){                 //
            this.styleUpwd={
                'upwd-focus':false,
                'dis-none':true,
            }; 
            this.eyeStyle={
                display:"block",
            };
            this.isupwd=1;
            this.upwdAuth="++++8";
            this.spanStyle={
                'span-sp':true,
                'dis-none':false,
            };
            var int1=/\d/.test(this.upwd);
            var int2=/[a-z]/.test(this.upwd);
            var int3=/[A-Z]/.test(this.upwd);
            var int4=/[,./?;]/.test(this.upwd);
            this.int5=/\s/.test(this.upwd);
            this.int6=/[\u4e00-\u9fa5]/.test(this.upwd);
            if(this.int5){
                this.upwdAuth="";
                return;
            };
            if(this.int6){
                this.upwdAuth="";
                return;  
            };
            if((int1==true&&int2==true)||(int1==true&&int3==true)||(int2==true&&int3==true)){
                this.isupwd=2;
            };
            if(int1==true&&int2==true&&int3==true&&int4){
                this.isupwd=3;
            };
        },
    },
    methods:{
        showPwd(){      //
            if(this.showImg==1){
                this.intType="text";
                this.showImg=2;
            }else{
                this.intType="password";
                this.showImg=1; 
            }
        },
        userReg(){                      // 
         console.log(this.int6,this.int5)
            if(!this.unameIstrue){
                this.$alert("","",{confirmButtonText: ''}).then(active=>{
                    return;
                }).catch(err=>{
                    return;
                })
            }else if(!((this.upwd.length>=8)&&this.int5==false&&this.int6==false)){
                this.$alert("","",{confirmButtonText: ''}).then(active=>{
                    return;
                }).catch(err=>{
                    return;
                })
            }else if(!this.authIstrue){
                this.$alert("","",{confirmButtonText: ''}).then(active=>{
                    return;
                }).catch(err=>{
                    return;
                })
            }else if(!this.isAgree){
                this.$alert("","",{confirmButtonText: ''}).then(active=>{
                    return;
                }).catch(err=>{
                    return;
                })
            }else{
                // var date=new URLSearchParams();
                // date.append('uname','this.uname');
                // date.append('upwd','this.upwd');
                // console.log(date);
                var regTime = new Date().toLocaleString('chinese',{hour12:false});
                    regTime = regTime.replace(/\//g,"-");
                var res=this.qs.stringify({
                    uname:this.uname,
                    upwd:this.upwd,
                    regTime:regTime,
                })
                console.log(res)
                this.axios.post("reg",
                    res
                ).then(result=>{
                        if(result.data.code==1){
                            this.$alert(",","",{confirmButtonText:''}).then(success=>{
                                this.$router.push("/login");
                            }).catch(err=>{
                                location.reload();
                            });
                        };
                    });
            };
        },
        pwdAuth(){          //
            if(this.pwd==this.upwd){
                this.msgPwd="";
                this.isTrue={     
                    'int-focus':true,
                    'int-success':true,
                    'int-err':false, 
                }
            }else{
                this.msgPwd="";
                this.isTrue={     
                    'int-focus':true,
                    'int-success':false,
                    'int-err':true, 
                }
            };
        },
        pwdBlur(){             //
            if(this.pwd==this.upwd&&this.pwd!=""){
                this.msgPwd="";
                this.isTrue={     
                    'int-focus':true,
                    'int-success':true,
                    'int-err':false, 
                };
                this.pwdIstrue=true;
            }else{
                this.msgPwd="";
                this.isTrue={     
                    'int-focus':true,
                    'int-success':false,
                    'int-err':true, 
                };
                this.pwdIstrue=false;
            };
        },
        upwdBlur(){     //
            this.styleUpwd={
                'upwd-focus':false,
                'dis-none':true,
            };
            this.spanStyle={
                'span-sp':true,
                'dis-none':false,
            };
            if(this.upwd==""){
                this.eyeStyle={
                    display:"none",
                };
            };
            this.upwdAuth="++++8";
            if((this.upwd.length>=8)&&this.int5==false&&this.int6==false){
                this.upwdIstrue=true;
            };
        },
        unameBlur(){        //
            var reg=/^1[3-8]\d{9}$/     //
            if(!reg.test(this.uname)){
                this.msgUname=""
                this.styleInt={
                    'int-focus':true,
                    'int-success':false,
                    'int-err':true,
                }
                this.unameIstrue=false;
            }else{
                this.msgUname=""
                this.styleInt={
                    'int-focus':true,
                    'int-success':true,
                    'int-err':false,
                };
                this.unameIstrue=true;
            }
        },
        pwdFocus(){       //
            this.msgPwd="";
            this.isTrue={
                'int-focus':true,
                'is-success':false,
                'is-err':false,
            };
        },
        upwdFocus(){    //
            this.msgUpwd="8-20()";
            this.styleUpwd={    //
                'upwd-focus':true,
                'dis-none':false,
            };
            this.spanStyle={    //
                'span-sp':false,
                'dis-none':true,
            };
            if(this.upwd!=""){
                this.eyeStyle={
                    display:"block",
                }
            };
        },
        unameFocus(){   //
            this.msgUname="";
            this.styleInt={         //
                'int-focus':true,
                'int-success':false,
                'int-err':false,
            }
        },
        changeImg(){    //
            this.imgNum++;
            if(this.imgNum>6){
                this.imgNum=1;
            }
        },
    },
    // mounted(){    
    //     this.widthout=window.outerWidth;
    //     console.log(this.widthout)
    // },
}
</script>
<style>
    @import url("../assets/css/reg.css");
</style>
