        <my-footer></my-footer>
        <login :spanHid="spanHid" :loginMsg="loginMsg" :log="log" :imgUrl="imgUrl" :changeImg="changeImg" :toLogin="toLogin" v-on:sendValue="(val)=>this.authCode=val" @sendName="(val)=>this.uname=val" @sendPwd="(val)=>this.upwd=val"></login>   <!---->
    </div>
</template>
<script>
// import listOne from '../components/listOne.vue'
// import listTwo from '../components/listTwo.vue'
// import listThree from '../components/listThree.vue'
// import listFour from '../components/listFour.vue'
// import listFive from '../components/listFive.vue'
export default {
    data(){
        return {
            log:null,    //
            uname:"",    //
            upwd:"",     //
            authCode:"", //
            arr:this.$store.state.arr,    //
            imgUrl:1,       //
            authTrue:false,     //
            oneList:[],
            twoList:[],
            threeList:[],
            fourList:[],
            fiveList:[],
            fiveListOne:{imgUrl:"gy/dnyf.jpg"},     //undefined
            sixList:[],
            sixListOne:{imgUrl:"gy/dnyf.jpg"},      // //undefined
            Sfamily:"",
            loginMsg:"",
        }
    },
    created(){
        // this.getName();
        this.getList();
            if(this.num==1){
                this.Sfamily="";
            }else if(this.num==2){
                this.Sfamily="";
            }else if(this.num==3){
                this.Sfamily="";
            }else if(this.num==4){
                this.Sfamily="";
            }else{
                this.Sfamily="";
            };
        // this.$router.go(0)
    },
    methods:{
        getList(){
            this.axios.get("list",{
                params:{
                    fid:this.num
                }
            }).then(result=>{
                var data=result.data.data
                this.oneList=data.slice(0,4);
                this.twoList=data.slice(4,13);
                this.threeList=data.slice(13,22);
                this.fourList=data.slice(22,28);
                this.fiveList=data.slice(0,10);
                this.sixList=data.slice(11,21);
                this.fiveListOne=this.fiveList[0];
                this.sixListOne=this.sixList[0];
                // console.log(this.fiveList,this.sixList)
            })
        },
        outLogin(){
            this.axios.get("outlogin").then(result=>{
                if(result.data.code==1){
                    location.reload();
                    this.$message("");
                    sessionStorage.removeItem("uname")
                    sessionStorage.removeItem("nickName");
                };
            });
        },
        toLogin(){          //
            if(this.uname==""&&this.upwd==""){
                this.loginMsg="";
                return;
            }else if(this.uname==""){
                this.loginMsg="";
                return;
            }else if(this.upwd==""){
                this.loginMsg="";
                return;
            }else if(this.authCode==""){
                this.loginMsg="";
                return;
            };
            if(!this.authTrue){
                this.loginMsg="";
            }else{
                this.axios.get("login",{
                    params:{
                        uname:this.uname,
                        upwd:this.upwd,
                    }
                }).then(res=>{
                    if(res.data.code==1){
                        var uname=(res.data.data[0]).slice(-4);
                        var nickName=res.data.data[1]
                        sessionStorage.setItem("uname","xxmy"+uname);
                        sessionStorage.setItem("nickName",nickName)
                        this.$alert("","",{confirmButtonText:''}).then(active=>{
                            this.log=null;
                            location.reload();
                        }).catch(err=>{
                            location.reload();
                             this.log=null;
                        });
                    }else{
                        this.$message.error("");
                    }
                });
            }
        },
        // getName(){
        //     this.axios.get("getname").then(result=>{
        //         if(result.data.code==1){
        //             this.loginName=result.data.data;
        //         }else{
        //             this.loginName="";
        //         }
        //     })
        // },
        changeImg(){        //
            this.imgUrl++;
            if(this.imgUrl==7){
                this.imgUrl=1; 
            };
        },
        login(){            //
            this.log=1;     
        },
        spanHid(){          //
            this.log=null;   
        },
    },
    watch:{
        authCode(){
            if(this.arr[this.imgUrl-1]==this.authCode){
                this.authTrue=true;
            }else{
                this.authTrue=false;
            };
        },
        num(){
            this.getList();
            if(this.num==1){
                this.Sfamily="";
            }else if(this.num==2){
                this.Sfamily="";
            }else if(this.num==3){
                this.Sfamily="";
            }else if(this.num==4){
                this.Sfamily="";
            }else{
                this.Sfamily="";
            };
        }
    },
    // computed:{
    //     listPic(){
    //         return `http://127.0.0.1:1994/${this.sixListOne.imgUrl}`
    //     }
    // },
    props:["num"],      //
    // components:{listOne,listTwo,listThree,listFour,listFive}
}
</script>
