<template>
    <div>
    <div class="top_nav">
        <div class="container">
            <div class="top_info">
                <ul>
                    <li style="display:none" :class="{liStyle:loginName==null}"><a href="javascript:;" @click="login"></a></li>
                    <li style="display:none" :class="{liStyle:loginName!=null}"><router-link to="/center" style="color:#f55;">{{loginName}}</router-link><a href="javascript:;" @click="outLogin"></a></li>
                    <li><router-link to="/reg"></router-link></li>
                    <li :class="{active:act}" @mouseover="bkShelf" @mouseout="bkShelf" style="width:86px;padding:0 8px 0 3px;">
                        <a href="javascript:;" @click="toShelf">
                            <span class="poin" :class="{useAnimate:act,useAnimate1:!act}"></span>
                        </a>
                    </li>
                    <li><a href=""></a></li>
                </ul>
                <div class="bkShelf" :class="{bkNone:act}" @mouseover="bkShelf" @mouseout="bkShelf">
                    <div class="bk-top"><span></span></div>
                </div>
            </div>
            <ul>
                <li>Android</li>
                <li>IOS</li>
                <li><router-link to="/"></router-link></li>
                <li><router-link to="/list/4" :class="{isActive:num==4}"></router-link></li>
                <li><router-link to="/list/2" :class="{isActive:num==2}"></router-link></li>
                <li><router-link to="/list/1" :class="{isActive:num==1}"></router-link></li>
                <li><router-link to="/list/5" :class="{isActive:num==5}"></router-link></li>
                <li><router-link to="/list/3" :class="{isActive:num==3}"></router-link></li>
                <li><router-link to="" ></router-link></li>
                <li><router-link to=""></router-link></li>
            </ul>
        </div>
    </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            loginName:sessionStorage.getItem("nickName")=="null"?sessionStorage.getItem("uname"):sessionStorage.getItem("nickName"),
            // bkNone:{display:"none"},
            act:false,
            // toShelf:this.$store.state.toShelf,
            // log:this.$store.state.log,    //
        }
    },
    created(){
        // this.loginName=this.$store.state.uname;
    },
    methods:{
        toShelf(){
            if(this.loginName){
                this.$router.push("/center")
            }else{
                this.$message("");
            }
        },
        bkShelf(){
            if(this.loginName!=null){
                this.act=!this.act;
                // this.bkNone={display:"block"};
            }
        },
    },
    props:{
        login:{type:Function},      //
        num:{default:""},       //
        // loginName:{default:""},       //
        outLogin:{type:Function},   //
    }
}
</script>

<style>
    @import url("../assets/css/myNav.css");
</style>
