<template>
    <div>
        <my-nav :login="login" :outLogin="outLogin"></my-nav>
        <div class="allBook-top">
            <div>
                <router-link to="/"><img src="image/index/logo.png" alt=""></router-link>
            </div>
            <div class="intStyle">
                <input type="text" v-model="search" @keyup.13="searchAllBook"><span @click="searchAllBook"><i></i><i></i></span>
            </div>
            <div class="imgStyle">
                <a href=""><img src="image/index/header_author.jpg" alt=""></a>
                <a href=""><img src="image/index/header_liwu.jpeg" alt=""></a>
            </div>
        </div>
        <hr style="margin-top:50px;"/>
        <div class="container">
            <div class="allBook-Content">
                <div class="contentList" v-for="(p,i) of allList" :key="i">
                    <div>
                        <router-link :to="`/detail/${p.sid}`"><img :src="`${url}${p.imgUrl}`" alt=""></router-link>
                    </div>
                    <div>
                        <div v-text="p.bname"></div>
                        <div v-text="p.author"></div>
                        <div> 
                              
                              
                              
                              
                             
                              
                               
                              
                              
                              
                            </div>
                        <router-link :to="`/detail/${p.sid}`"></router-link>
                    </div>
                </div>
            </div>
            <div class="intr-all">
                <button @click="toAct(-1)" :disabled="leftBtn" v-show="isHidden"></button>
                <ul>
                    <!-- <li v-show="">...</li> -->
                    <li :class="{active:allAct==i}" v-for="i in count" :key="i" @click="changeAct(i)">{{i}}</li>
                    <!-- <li>...</li> -->
                </ul>
                <button @click="toAct(+1)" :disabled="rightBtn" v-show="isHidden"></button>
            </div>
        </div>
        <my-footer></my-footer>
        <login :spanHid="spanHid" :loginMsg="loginMsg" :log="log" :imgUrl="imgUrl" :changeImg="changeImg" :toLogin="toLogin" v-on:sendValue="(val)=>this.authCode=val" @sendName="(val)=>this.uname=val" @sendPwd="(val)=>this.upwd=val"></login>   <!---->
    </div>
</template>
<script>
import Event from '../Even.js';
import { setTimeout } from 'timers';
export default {
    data(){
        return {
            url:"http://127.0.0.1:1994/",
            arr:this.$store.state.arr,    //
            log:this.$store.state.log,    //
            imgUrl:Math.ceil(Math.random()*6),       //
            authTrue:false,     //
            loginMsg:"",
            uname:"",    //
            upwd:"",     //
            authCode:"", //
            search:"",
            allList:[],
            count:1,
            allAct:1,
            isHidden:true,
            // liHidden:7,
            // isClick:true,

        }
    },
    watch:{
        search(){
            this.searchAllBook();
        },
        authCode(){
            if(this.arr[this.imgUrl-1]==this.authCode){
                this.authTrue=true;
            }else{
                this.authTrue=false;
            }
        },
        allAct(){
            this.searchAllBook();
        }
    },
    created(){
        // Event.$on("send",(val)=>{
        //     console.log(val)
        //     this.search=val;
        // }) 
        this.search=this.$store.state.search;
    },
    methods:{
        toAct(i){
            this.allAct+=i;
        },
        changeAct(i){
            this.allAct=i;
        },
        searchAllBook(){
            this.axios.get("search",{
                params:{
                    search:this.search,
                    pageNum:this.allAct,
                }
            }).then(res=>{
                this.allList=res.data.data.data;
                this.count=res.data.data.count;
                if(this.count==0){
                    this.isHidden=false;
                }else{
                    this.isHidden=true;
                }
            })
        },
        outLogin(){         //
            this.axios.get("outlogin").then(result=>{
                if(result.data.code==1){
                    location.reload();
                    this.$message("");
                    sessionStorage.removeItem("uname");
                    sessionStorage.removeItem("nickName");
                };
            });
        },
        login(){            //
            this.log=1;   
        },
        spanHid(){          //
            this.log=null;   
        },
        changeImg(){        //
            this.imgUrl++;
            if(this.imgUrl==7){
                this.imgUrl=1; 
            };
        },
        toLogin(){          //
            if(this.uname==""&&this.upwd==""){
                this.loginMsg="";
                return;
            }else if(this.uname==""){
                this.loginMsg="";
                return;
            }else if(this.upwd==""){
                this.loginMsg="";
                return;
            }else if(this.authCode==""){
                this.loginMsg="";
                return;
            };
            if(!this.authTrue){
                this.loginMsg="";
            }else{
                this.axios.get("login",{
                    params:{
                        uname:this.uname,
                        upwd:this.upwd,
                    }
                }).then(res=>{
                    if(res.data.code==1){
                        var uname=(res.data.data[0]).slice(-4);
                        var nickName=res.data.data[1]
                        console.log(uname,nickName)
                        sessionStorage.setItem("uname","xxmy"+uname);
                        sessionStorage.setItem("nickName",nickName)
                        this.$alert("","",{confirmButtonText:''}).then(active=>{ 
                            this.log=null;
                            location.reload();
                        }).catch(err=>{
                            this.log=null;
                            location.reload();
                        });
                    }else{
                        this.$message.error("");
                    }
                });
            }
        },
    },
    mounted(){
        this.searchAllBook();
    },
    computed:{
        leftBtn(){
            return this.allAct==1;
        },
        rightBtn(){
            return this.allAct==this.count
        }
    }
}
</script>>
<style>
    @import url("../assets/css/allList.css");
</style>