import Vue from 'vue'
import Router from 'vue-router'
// import Home from './views/Home.vue'

//
import Index from './views/Index.vue'

//
import StoryList from './views/StoryList.vue'

//
import Details from './views/details.vue'

//
import reg from './views/reg.vue'

//
import Login from './views/UserLogin.vue'

//
import userCenter from './views/BookShelf.vue'

//
import alllist from './views/allList.vue'
Vue.use(Router)

export default new Router({
  routes: [
    {path:'/',component:Index},     //
    {path:'/list/:num',component:StoryList,props:true},     //
    {path:'/detail/:sid',component:Details,props:true},     //
    {path:'/reg',component:reg},    //
    {path:'/login',component:Login},    //
    {path:'/center',component:userCenter},     //
    {path:'/allbook',component:alllist}    //
  ],
  scrollBehavior(to,from,savedPosition){
    return {
      x:0,
      y:0
      }
    }
})
